  display: block;
  padding: 15px 0;
  transition: all .3s;
  cursor: pointer;
}

.nav ul li a::before {
  content: '';
  position: absolute;
  transform: scale(0);
  transition: all .3s;
  bottom: 0;
  width: 100%;
  height: 2px;
  background: #f0c08d;
}

.nav ul li a:hover, .nav ul li a:focus {
  transition: color 0.3s 0.1s ease;
