  display: none;
  width: 50px;
  min-height: 50px;
  position: fixed;
  top: 10px;
  right: 10px;
  background-color: #2e62a3;
  border-radius: 50%;
}

@media only screen and (max-width: 768px) {
  .nav__button {
    display: block;
  }
  .nav__button.active {
    transition: all .3s;
  }
  .nav__button.active .bar {
    transition: all .25s;
    background-color: transparent;
  }
  .nav__button.active .bar:before {
    transition: all .25s;
    transform: rotate(45deg);
    top: 0;
  }
  .nav__button.active .bar:after {
    top: 0;
    transition: all .25s;
    transform: rotate(-45deg);
  }
}

.nav__button .bar {
  position: fixed;
  right: 25px;
  top: 32px;
  transition: all .25s;
  content: "";
  height: 3px;
  width: 20px;
  background-color: #ffffff;
}

.nav__button .bar:before {
  top: -8px;
  transition: all .25s;
  content: "";
  position: absolute;
  right: -3px;
  height: 3px;
  width: 26px;
  background-color: #ffffff;
}

.nav__button .bar:after {
  top: 8px;
  transition: all .25s;
  content: "";
  position: absolute;
  right: -3px;
  height: 3px;
  width: 26px;
  background-color: #ffffff;
}

